<template>

  <div style="height: 100%; overflow: hidden">
    <div style="width: 100%; overflow: hidden">
      <menu-bar/>
      <tool-bar/>
    </div>

    <div style="height: 100%">
      <div style="width: 300px; left: 0;" class="column"><setting-panel style="width: 100%; margin-bottom: 75px"/></div>
      <div style="left: 300px; right: 200px;background-color: #8c939d" class="column"><model/></div>
      <div style="width: 200px; right: 0" class="column"><attribute-panel style="width: 100%"/></div>
    </div>
  </div>
</template>

<script>
import MenuBar from "./MenuBar";
import ToolBar from "./ToolBar";
import SettingPanel from "./SettingPannel";
import AttributePanel from "./AttributePanel";
import Model from "./Model";

export default {
  name: "HomePage",
  components: {Model, AttributePanel, SettingPanel, ToolBar, MenuBar},
  data() {
    return {}
  }
}
</script>

<style scoped>
.column {
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  top: 75px;
  margin-bottom: 75px;
  z-index: 0;
}
</style>

<style>
.el-header {
  padding: 0;
}
</style>


