<template>
  <div>
    <div style="display: flex; justify-content: space-between">
      <h5 class="setting-header">六面体Polycube</h5>
      <el-link type="primary" class="show-model-link" @click="showModel" :disabled="model.processing" style="visibility: hidden">显示模型</el-link>
    </div>

    <el-form label-position="right" size="mini" label-width="80px" :disabled="model.processing">
      <el-form-item label="X轴放缩">
        <el-input class="setting-input"  type="number" v-model="form.scaling_X">
        </el-input>
      </el-form-item>
      <el-form-item label="Y轴放缩">
        <el-input class="setting-input"  type="number" v-model="form.scaling_Y">
        </el-input>
      </el-form-item>
      <el-form-item label="Z轴放缩">
        <el-input class="setting-input"  type="number" v-model="form.scaling_Z">
        </el-input>
      </el-form-item>
      <el-form-item label="边缘平滑">
        <el-input class="setting-input"  type="number" v-model="form.HexCubePerfector.smooth_boundary"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="表面平滑">
        <el-input class="setting-input"  type="number" v-model="form.HexCubePerfector.smooth_surface"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="体平滑">
        <el-input class="setting-input"  type="number" v-model="form.HexCubePerfector.smooth_volume"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="Pillowing X">
        <el-radio class="setting-input" v-model="form.Pillowing.X" :label="true">打开</el-radio>
        <el-radio class="setting-input" v-model="form.Pillowing.X" :label="false">关闭</el-radio>
      </el-form-item>
      <el-form-item label="Pillowing Y">
        <el-radio class="setting-input" v-model="form.Pillowing.Y" :label="true">打开</el-radio>
        <el-radio class="setting-input" v-model="form.Pillowing.Y" :label="false">关闭</el-radio>
      </el-form-item>
      <el-form-item label="Pillowing Z">
        <el-radio class="setting-input" v-model="form.Pillowing.Z" :label="true">打开</el-radio>
        <el-radio class="setting-input" v-model="form.Pillowing.Z" :label="false">关闭</el-radio>
      </el-form-item>

    </el-form>

    <div style="text-align: right; display: none">
      <el-button type="warning" icon="el-icon-refresh-left" size="mini" round>重置</el-button>
      <el-button type="success" icon="el-icon-upload" size="mini" round>保存</el-button>
      <el-button type="primary" icon="el-icon-video-play" size="mini" round>应用</el-button>
    </div>

  </div>
</template>

<script>
export default {
  name: "ConfigHexahedralPolycube",
  props: ["defaultForm", "model"],
  data(){
    return{
      form: JSON.parse(JSON.stringify(this.defaultForm))
      // form: {
        // HexPolycube: {
        //   scaling_X: 0.055,
        //   scaling_Y: 0.055,
        //   scaling_Z: 0.055,
        //   merge_threshold: 0.2,
        //   HexCubePerfector: {
        //     smooth_boundary: 5,
        //     smooth_surface: 20,
        //     smooth_volume: 20
        //   },
        //   Pillowing: {
        //     X: true,
        //     Y: true,
        //     Z: false
        //   },
        // }
      // }
    }
  },
  methods: {
    showModel(){
      this.$EventBus.$emit("model", {action: "load", model: "hex-polycube"});
    }
  }
}
</script>

<style scoped>

</style>


