<template>
  <el-tooltip effect="dark" :content="prompt" placement="bottom">
    <input class="icon" type="image" :src="m_image"
           v-on:mouseover="onMouseOver"
           v-on:mouseout="onMouseOut" alt/>
  </el-tooltip>
</template>

<script>
export default {
name: "ToolItemButton",
  props: ['prompt', 'normalImage', 'activeImage'],
  data() {
    return {
      m_image: this.normalImage
    }
  },
  methods: {
    onMouseOver(e){
      this.m_image = this.activeImage;
    },
    onMouseOut(e){
      this.m_image = this.normalImage;
    }
  }
}
</script>

<style scoped>
.icon {
  outline: none;
  margin: 4px 6px;
  padding: 6px;
  height: 18px;
  width: 18px;
  border-radius: 10%;
}

.icon:hover {
  background-color: rgb(217, 94, 41);
}
</style>


