<template>
  <el-container id="model-area"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <VueDraggableResize id="model-stats" :w="80" :h="48"
                        :y="80" :x="statsPositionX"
                        :handle-info="{size: 0,offset: 0,switch: true}"
                        :isResizable="false">
    </VueDraggableResize>

    <script id="vshader" type="x-shader/x-vertex">
    precision highp float;
    uniform mat4 modelViewMatrix;
    uniform mat4 projectionMatrix;
    uniform float time;

    attribute vec3 position;
    attribute vec3 translate;
    attribute vec3 vertexColor;

    varying vec3 vVertexColor;

    void main() {
        vec4 mvPosition = modelViewMatrix * vec4(translate, 1.0);
        vec3 trTime = vec3(
            translate.x + time,
            translate.y + time,
            translate.z + time
        );
        float scale = 1.0;
        scale = scale * 10.0 + 10.0;
        mvPosition.xyz += position * scale;
        vVertexColor = vertexColor;
        gl_Position = projectionMatrix * mvPosition;
    }

    </script>
    <script id="fshader" type="x-shader/x-fragment">
    precision highp float;
    varying vec3 vVertexColor;

    void main() {
        gl_FragColor = vec4(vVertexColor, 1);
    }

    </script>
  </el-container>
</template>

<script>
import * as THREE from "three";
import VueDraggableResize from 'vue-draggable-resizable-gorkys'
import axios from "axios";

let STLLoader = require('three-stl-loader')(THREE);
let OrbitControls = require('three-orbit-controls')(THREE);
let Stats = require('stats.js');
export default {
  name: "Model",
  components: {VueDraggableResize},
  data() {
    return {
      loading: true,
      camera: null,
      cameraTarget: null,
      scene: null,
      renderer: null,
      axisHelper: null,
      stats: null,
      mesh: null,
      dummy: new THREE.Object3D()
    }
  },
  computed: {
    statsPositionX() {
      return window.innerWidth - 80 - 20
    }
  },
  mounted() {
    this.init();
    function getQueryString(key) {
      var query = window.location.search.substring(1);
      var map = query.split("&");
      for (var i = 0; i < map.length; i++) {
        var pair = map[i].split("=");
        if (pair[0] === key) {
          return pair[1];
        }
      }
    }

    let query = getQueryString("model");
    console.log(query);
    if(query === "stl"){
      this.loadStlObject();
    }else{
      this.loadInstancedObject();
    }

    // this.loadStlObject();
    // this.loadJSONObject();
    // this.loadInstancedObject();
    // this.loadInstancedColorExample();

    // this.loadJSONObject();

    // this.loadManualInstancedObject();
    // this.loadMultiInstancedObject();
    // this.loadLODObject();
    // this.loadLODExample();
    this.createLights();
    this.animate();
    this.actionInit();
  },
  methods: {
    init() {
      let height = window.innerHeight;
      let width = window.innerWidth;
      let container = document.getElementById("model-area");
      this.camera = new THREE.PerspectiveCamera(35, width / height, 0.01, 15000);
      // this.camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 150);
      this.camera.position.set(3, 0.15, 3);
      // this.camera.position.z = 250;
      // this.camera.position.set(30, 1.5, 30);
      this.cameraTarget = new THREE.Vector3(0, -0.25, 0);

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x555555);
      // this.scene.fog = new THREE.Fog(0x555555, 2, 15);

      this.renderer = new THREE.WebGLRenderer({antialias: true});
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(width, height);
      this.renderer.outputEncoding = THREE.sRGBEncoding;

      this.renderer.shadowMap.enabled = true;

      container.appendChild(this.renderer.domElement);

      window.addEventListener('resize', this.onWindowResize, false);

      let controls = new OrbitControls(this.camera, this.renderer.domElement);//创建控件对象
      controls.addEventListener('change', this.render);//监听鼠标、键盘事件

      this.axisHelper = new THREE.AxisHelper(0.15);
      this.scene.add(this.axisHelper);

      this.stats = new Stats();  // 创建一个性能监视器
      this.stats.domElement.style.position = 'absolute';  // 样式， 坐标
      console.log(container)
      document.getElementById('model-stats').appendChild(this.stats.domElement);
    },
    make_center(geometry) {
      var cols = geometry.attributes.position.itemSize;
      var rows = geometry.attributes.position.count;
      var mean = Array(cols).fill(0);
      for (let i = 0; i < rows; ++i) {
        for (let j = 0; j < cols; ++j) {
          mean[j] += geometry.attributes.position.array[i * cols + j];
        }
      }
      for (let j = 0; j < cols; ++j) {
        mean[j] /= rows;
      }
      geometry.computeBoundingBox();
      var arr = geometry.boundingBox.max.clone().sub(geometry.boundingBox.min.clone());
      var scale = Math.max(...[arr.x, arr.y, arr.z]);

      for (let i = 0; i < rows; ++i) {
        for (let j = 0; j < cols; ++j) {
          geometry.attributes.position.array[i * cols + j] -= mean[j];
          geometry.attributes.position.array[i * cols + j] /= scale;
        }
      }
      // geometry.attributes.position.array.map(x => x / scale);
      geometry.computeBoundingBox();
    },

    loadStlObject() {
      new STLLoader().load('./static/models/porous.stl', geometry => {
        this.make_center(geometry);
        console.log(geometry)
        geometry = new THREE.Geometry().fromBufferGeometry(geometry);
        // console.log(geometry);

        let material = new THREE.MeshPhongMaterial({color: 0x777777, specular: 0, shininess: 20});
        let mesh = new THREE.Mesh(geometry, material);
        mesh.position.set(0, 0, 0);
        mesh.rotation.set(0, -Math.PI / 2, 0);
        mesh.scale.set(2,2,2);

        mesh.castShadow = true;
        mesh.receiveShadow = true;
        console.log(mesh);
        this.scene.add(mesh);
        this.loading = false;
      });
    },
    loadInstancedObject() {
      this.axios.get("/static/models/matrix4.json").then(res => {
        this.rod_meshes = []

        let material = new THREE.MeshPhysicalMaterial({color: 0x777777})
        // let material = new THREE.MeshPhongMaterial({
        //   metalness: 1.0,
        //   roughness: 0.6,
        //   vertexColors: THREE.VertexColors,
        // });

        let rods = res.data.rods;

        rods.forEach((rod, rod_id) => {
          let ratio = rod[0];
          let rod_transformers = rod[1];

          let rod_n_matrix = rod_transformers.length;
          let rod_geometry = new THREE.CylinderBufferGeometry(ratio, 1, 2, 5, 3);

          rod_geometry.colorsNeedUpdate = true;
          rod_geometry.elementsNeedUpdate = true;
          rod_geometry.computeBoundingSphere();

          // const _color = new THREE.Color();
          // const color = new Float32Array( rod_n_matrix * 3 );
          // const palette = [ 0xF20587, 0xF2D479, 0xF2C879, 0xF2B077, 0xF24405 ];
          //
          // for ( let i = 0; i < rod_n_matrix; i ++ ) {
          //   _color.setHex( palette[ Math.floor( Math.random() * palette.length ) ] );
          //   _color.toArray( color, i * 3 );
          // }
          //
          // rod_geometry.setAttribute( 'color', new THREE.InstancedBufferAttribute( color, 3 ) );
          // material.vertexColors = true;

          let rod_mesh = new THREE.InstancedMesh(rod_geometry, material, rod_n_matrix);
          rod_mesh.instanceMatrix.setUsage(THREE.StaticReadUsage);

          console.log(rod_mesh)

          rod_mesh.position.set(0, 0, 0);
          // this.mesh.rotation.set(Math.PI / 2, 0, Math.PI / 2);
          rod_mesh.scale.set(2,2,2);
          rod_mesh.castShadow = true;
          rod_mesh.receiveShadow = true;
          this.scene.add(rod_mesh);

          for (let i = 0; i < rod_n_matrix; i++) {
            let t = new THREE.Matrix4();
            t.set(...rod_transformers[i])
            rod_mesh.setMatrixAt(i, t);
          }

          rod_mesh.instanceMatrix.needsUpdate = true;
          this.rod_meshes.push(rod_mesh);
        })
        // let rod_transformers = res.data.rods;

        // for (let i in rod_geometry.faces) {
        //   // let face = rod_geometry.faces[i];
        //   // let f = new THREE.Face3(face[0], face[1], face[2]);
        //   rod_geometry.faces[i].vertexColors = [new THREE.Color(0xff0000), new THREE.Color(0xff0000), new THREE.Color(0xff0000)];
        //   rod_geometry.faces[i].vertexColors[0].setRGB(1, 0.4, 0.2);
        //   rod_geometry.faces[i].vertexColors[1].setRGB(1, 0.4, 0.2);
        //   rod_geometry.faces[i].vertexColors[2].setRGB(1, 0.4, 0.2);
        // }
        // console.log(rod_geometry)
        let ball_transformers = res.data.balls;
        let ball_n_matrix = ball_transformers.length;
        let ball_geometry = new THREE.SphereBufferGeometry(1, 8, 8)

        ball_geometry.colorsNeedUpdate = true;
        ball_geometry.elementsNeedUpdate = true;
        ball_geometry.computeBoundingSphere();

        this.ball_mesh = new THREE.InstancedMesh(ball_geometry, material, ball_n_matrix);
        this.ball_mesh.instanceMatrix.setUsage(THREE.StaticReadUsage);

        this.ball_mesh.position.set(0, 0, 0);
        // this.mesh.rotation.set(Math.PI / 2, 0, Math.PI / 2);
        this.ball_mesh.scale.set(2,2,2);
        this.ball_mesh.castShadow = true;
        this.ball_mesh.receiveShadow = true;
        this.scene.add(this.ball_mesh);

        // let dummy = new THREE.Object3D()
        // dummy.scale.set(4,4,4);
        // dummy.position.set(1.1,1.2,1.3);
        // dummy.updateMatrix()

        for (let i = 0; i < ball_n_matrix; i++) {
          let t = new THREE.Matrix4();
          t.set(...ball_transformers[i])
          // console.log("dummy")
          // console.log(dummy.matrix)
          this.ball_mesh.setMatrixAt(i, t);
        }
        this.ball_mesh.instanceMatrix.needsUpdate = true;


        this.loading = false;
        let geometryByteLength = this.getGeometryByteLength(rod_geometry);
        this.$message([
          'Number of Geometry: ' + String(rod_n_matrix),
          'GPU draw calls: 1',
          'GPU memory: ' + this.formatBytes(rod_n_matrix * 16 + geometryByteLength, 2)
        ].join('\n'));

      })
    },
    loadInstancedColorExample() {
      let cubeGeo = new THREE.InstancedBufferGeometry().copy(new THREE.BoxBufferGeometry(10, 10, 10));
//cubeGeo.maxInstancedCount = 8;

      cubeGeo.addAttribute("cubePos", new THREE.InstancedBufferAttribute(new Float32Array([
        25, 25, 25,
        25, 25, -25, -25, 25, 25, -25, 25, -25,
        25, -25, 25,
        25, -25, -25, -25, -25, 25, -25, -25, -25
      ]), 3, 1));

      let vertexShader = [
        "precision highp float;",
        "",
        "uniform mat4 modelViewMatrix;",
        "uniform mat4 projectionMatrix;",
        "",
        "attribute vec3 position;",
        "attribute vec3 cubePos;",
        "",
        "void main() {",
        "",
        "	gl_Position = projectionMatrix * modelViewMatrix * vec4( cubePos + position, 1.0 );",
        "",
        "}"
      ].join("\n");
      let fragmentShader = [
        "precision highp float;",
        "",
        "void main() {",
        "",
        "	gl_FragColor = vec4(1.0, 0.0, 0.0, 1.0);",
        "",
        "}"
      ].join("\n");

      let mat = new THREE.RawShaderMaterial({
        uniforms: {},
        vertexShader: vertexShader,
        fragmentShader: fragmentShader,
        side: THREE.DoubleSide,
        transparent: false
      });

      let mesh = new THREE.Mesh(cubeGeo, mat);
      console.log(mesh)
      this.scene.add(mesh);

      this.loading = false;
    },
    createLights() {
      // this.scene.add(new THREE.HemisphereLight(0x443333, 0x111122));
      this.scene.add(new THREE.AmbientLight(0x606060));
      this.addShadowedLight(-1, 1, 1, 0xeeeeee, 1);
      this.addShadowedLight(-0.5, 0.5, -1, 0xcccccc, 0.5);
      this.addShadowedLight(1, 0.5, 0, 0xcccccc, 1);
    },
    addShadowedLight(x, y, z, color, intensity) {
      let directionalLight = new THREE.DirectionalLight(color, intensity);
      directionalLight.position.set(x, y, z);
      this.scene.add(directionalLight);

      directionalLight.castShadow = true;

      let d = 1;
      directionalLight.shadow.camera.left = -d;
      directionalLight.shadow.camera.right = d;
      directionalLight.shadow.camera.top = d;
      directionalLight.shadow.camera.bottom = -d;

      directionalLight.shadow.camera.near = 1;
      directionalLight.shadow.camera.far = 4;

      directionalLight.shadow.bias = -0.002;
    },
    render() {
      this.camera.lookAt(this.cameraTarget);
      this.camera.updateMatrixWorld();
      let axesPlacement = this.camera.localToWorld(new THREE.Vector3(0.45 * this.camera.aspect, -0.4, -2));
      this.axisHelper.position.copy(axesPlacement);
      this.renderer.render(this.scene, this.camera);

      // this.renderer.render(this.scene, this.camera);
    },
    onWindowResize() {
      this.camera.aspect = window.innerWidth / (window.innerHeight - 60);
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(window.innerWidth, window.innerHeight - 60);
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.render();
      this.stats.update();
    },
    actionInit() {
      this.$EventBus.$on("model", (msg) => {
        switch (msg.action) {
          case "reset":
            this.camera.position.set(3, 0.15, 3);
            this.cameraTarget = new THREE.Vector3(0, -0.25, 0);
            this.$message({
              message: '复位成功',
              type: 'success'
            });
            break;
          default:
            this.$message({
              message: '收到未定义的事件: ' + msg.action,
              type: 'warning'
            });
        }
      });
    },
    getGeometryByteLength(geometry) {
      let total = 0;
      if (geometry.index) total += geometry.index.array.byteLength;
      for (let name in geometry.attributes) {
        total += geometry.attributes[name].array.byteLength;
      }
      return total;
    },
    formatBytes(bytes, decimals) {
      if (bytes === 0) return '0 bytes';
      let k = 1024;
      let dm = decimals < 0 ? 0 : decimals;
      let sizes = ['bytes', 'KB', 'MB'];
      let i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
    }
  }
}
</script>

<style scoped>

</style>



