<template>
  <div v-loading="loading"
       element-loading-text="Loading"
       element-loading-spinner="el-icon-loading"
       element-loading-background="rgba(0, 0, 0, 0.8)"
       style="height: 100%; display: flex; flex-direction: column">
    <div>
      <h4 class="attr-header">文件列表</h4>
      <el-divider></el-divider>

      <div v-if="fileList.length === 0" style="font-size: 14px; text-align: center; margin-top: 30px">
        暂时没有数据，
        <el-link type="primary" @click="loadingData"><i class="el-icon-refresh"></i>点击刷新</el-link>
      </div>

      <div class="file-list">
        <el-popover
            v-for="file in fileList"
            :key="file.id"
            placement="left"
            width="40"
            trigger="hover">
          <div class="file-info">
            <div style="font-size: 16px"><strong>{{ file.name }}</strong></div>
            <div style="font-size: 12px"> {{ new Date(file.created).format("yyyy/MM/dd hh:mm:ss") }}</div>
<!--            <div>长度: {{ file.info.length }} mm</div>-->
<!--            <div>宽度: {{ file.info.width }} mm</div>-->
<!--            <div>高度: {{ file.info.height }} mm</div>-->
<!--            <div>面积: {{ file.info.area }} mm²</div>-->
<!--            <div>体积: {{ file.info.volume }} mm³</div>-->
            <div v-if="file.processing" style="color: rgb(217, 94, 41)">
              <i class="el-icon-loading"></i> 生成多孔模型中
<!--              <div v-if="file.target === 1"><i class="el-icon-loading"></i> 生成四面体中</div>-->
<!--              <div v-else-if="file.target === 2"><i class="el-icon-loading"></i> 生成六面体中</div>-->
<!--              <div v-else-if="file.target === 3"><i class="el-icon-loading"></i> 生成多孔模型中</div>-->
<!--              <div v-else style="color: red"><i class="el-icon-warning"></i> 错误的状态</div>-->
            </div>
            <div style="text-align: right">
              <el-link type="danger" style="font-size: 12px" @click="deleteModel(file)">删除模型</el-link>
            </div>

          </div>
          <div class="file-item-wrapper" slot="reference">
            <el-link :class="'file-item ' + ((active_id+'') === (file.id+'')? 'active' : '') " :underline="false"
              @click="gotoNewPage(file.id)">
              {{ file.name }}
              <i class="el-icon-loading" v-if="file.processing" style="color: rgb(217, 94, 41)"></i></el-link>
          </div>
        </el-popover>
        <div v-if="fileList.length !== 0" style="font-size: 14px; text-align: center; margin-top: 15px">
          <el-link type="primary" @click="loadingData"><i class="el-icon-refresh"></i>点击刷新</el-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AttributePanel",
  data() {
    return {
      active_id: null,
      loading: false,
      fileList: []
    }
  },
  mounted() {
    this.active_id = this.$route.params.id;
    this.loadingData();
  },
  methods: {
    loadingData() {
      this.loading = true;
      this.axios.get(this.GLOBAL.MODELS_LIST_URL).then(response => {
        let data = response.data;
        if (data.status !== 200) {
          this.$message.error('错误：' + data.msg);
          return;
        }
        this.fileList = data.data;
        this.loading = false;
      }).catch(error => {
        this.$message.error('文件列表拉取连接异常：' + error);
        this.loading = false;
      })
    },
    gotoNewPage(id){
      this.$router.replace({
        name: 'Detail',
        params: {
          id: id
        }
      })
    },
    deleteModel(file){
      this.$confirm('此操作将永久删除模型"' + file.name + '" , 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true;
        this.axios.delete(this.GLOBAL.MODEL_ITEM_URL(file.id)).then(response => {
          let data = response.data;
          if (data.status !== 200) {
            this.$message.error('删除失败: ' + data.msg);
          }else{
            this.$message.success("删除成功!");
          }
          this.loading = false;
          this.loadingData();
        }).catch(error => {
          this.$message.error('通讯异常：' + error);
          this.loading = false;
        })
      })
    }
  }
}
</script>

<style scoped>
.attr-header {
  /*padding-left: 10px;*/
  margin: 12px 0 0 15px;
}

.attr-subheader {
  margin: 12px 0;
}

.place-setting {
  padding: 2px 0 0 15px;
  overflow: scroll;
}

.file-list {
  margin-top: 10px;
  /*height: 150px;*/
  height: 100%;
  overflow: scroll;
}

.file-item.active {
  color: rgb(217, 100, 47);
}

.file-item.active, .file-item {
  font-size: 14px;
  margin: 0 0 0 15px;
  padding-top: 2px;
  padding-bottom: 2px;
}

.file-item-wrapper {
  display: block;
}

.file-item-wrapper.active {
  background-color: rgb(249, 225, 204);
}

.file-info {
  font-size: 12px;
}

.file-info > div {
  margin: 5px;
}

.attr-label {
  /*font-size: 14px;*/
}

.attr-item {
  font-size: 14px;
  display: flex;
  justify-content: space-evenly;
}
</style>

<style>
.el-link.el-link--default:hover {
  color: rgb(217, 100, 47);
}

.el-divider--horizontal {
  margin: 8px 2px;
  width: 95%;
}
</style>


