<template>
  <div style="display: flex; justify-content: space-between">
    <h5 class="setting-header">优化模型</h5>
    <el-link type="primary" class="show-model-link" @click="showModel">显示模型</el-link>
  </div>
</template>

<script>
export default {
  name: "ConfigOptimizer",
  methods: {
    showModel() {
      this.$EventBus.$emit("model", {action: "load", model: "optimizer"});
    }
  }
}
</script>

<style scoped>

</style>


