<template>
  <div>
    <div style="display: flex; justify-content: space-between">
      <h5 class="setting-header">四面体Polycube</h5>
      <el-link type="primary" class="show-model-link" @click="showModel" :disabled="model.processing" style="visibility: hidden">显示模型</el-link>
    </div>

    <el-form label-position="right" size="mini" label-width="80px" :disabled="model.processing">
      <el-form-item label="alpha">
        <el-input class="setting-input"  type="number" v-model="form.alpha">
        </el-input>
      </el-form-item>
      <el-form-item label="beta">
        <el-input class="setting-input"  type="number" v-model="form.beta"></el-input>
      </el-form-item>
      <el-form-item label="eq_tol">
        <el-input class="setting-input"  type="number" v-model="form.eq_tol"></el-input>
      </el-form-item>
      <el-form-item label="eps">
        <el-input class="setting-input"  type="number" v-model="form.eps"></el-input>
      </el-form-item>
      <el-form-item label="首次迭代">
        <el-input class="setting-input" type="number" v-model="form.start_iter"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="最大迭代">
        <el-input class="setting-input"  type="number" v-model="form.max_iter"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="每步放缩">
        <el-input class="setting-input"  type="number" v-model="form.iter_step_scal">
        </el-input>
      </el-form-item>
      <el-form-item label="全局循环">
        <el-input  class="setting-input" type="number" v-model="form.global_iter"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="边缘平滑">
        <el-input  class="setting-input" type="number" v-model="form.TetCubePerfector.smooth_boundary"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="表面平滑">
        <el-input  class="setting-input" type="number" v-model="form.TetCubePerfector.smooth_surface"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
      <el-form-item label="体平滑">
        <el-input  class="setting-input" type="number" v-model="form.TetCubePerfector.smooth_volume"
                  onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8">
        </el-input>
      </el-form-item>
    </el-form>

    <div style="text-align: right; display: none">
      <el-button type="warning" icon="el-icon-refresh-left" size="mini" round>重置</el-button>
      <el-button type="success" icon="el-icon-upload" size="mini" round>保存</el-button>
      <el-button type="primary" icon="el-icon-video-play" size="mini" round>应用</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ConfigTetrahedralPolycube",
  props: ["defaultForm", "model"],
  data() {
    return {
      form: JSON.parse(JSON.stringify(this.defaultForm))
      // form: {
      //   TetPolycube: {
      //     alpha: 6,
      //     beta: 4,
      //     eq_tol: 0,
      //     eps: 0.1,
      //     start_iter: 20,
      //     max_iter: 300,
      //     iter_step_scal: 1.4,
      //     global_iter: 4,
      //     TetCubePerfector: {
      //       smooth_boundary: 5,
      //       smooth_surface: 20,
      //       smooth_volume: 20
      //     },
      //   }
      // }
    }
  },
  methods: {
    showModel(){
      this.$EventBus.$emit("model", {action: "load", model: "tet-polycube"});
    }
  }
}
</script>

<style scoped>

</style>


