<template>
  <div class="tool-bar">
    <el-tabs v-model="activeName">
      <el-tab-pane v-for="item in toolBarItems" :label="item.label" :name="item.name" :key="item.label">
<!--        <tool-item-button v-if="item.tools.length === 0" prompt="placeholder"-->
<!--                          active-image="" normal-image="" style="visibility: hidden"/>-->
<!--        <tool-item-button v-for="tool in item.tools" :prompt="tool.prompt" :key="tool.prompt"-->
<!--                          :active-image="tool.activeImage" :normal-image="tool.normalImage"/>-->
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import ToolItemButton from "./buttons/ToolItemButton";

export default {
  name: "ToolBar",
  components: {ToolItemButton},
  data() {
    return {
      activeName: 'file',
      toolBarItems: [
        {
          label: "文件",
          name: "file",
          tools: [
            {
              prompt: "打开",
              activeImage: "/static/images/icons/icon_open_normal.png",
              normalImage: "/static/images/icons/icon_open_deleted.png",
            },
          ]
        },
        {
          label: "编辑", name: "edit",
          tools: []
        },
        {
          label: "视图", name: "view",
          tools: []
        },
        {
          label: "纹理", name: "texture",
          tools: []
        },
        {
          label: "晶格", name: "lattice",
          tools: []
        },
        {
          label: "性能分析", name: "performance-analysis",
          tools: []
        },
        {
          label: "性能优化", name: "performance-optimization",
          tools: []
        },
        {
          label: "渲染", name: "rendering",
          tools: []
        }
      ]
    };
  },
}
</script>

<style scoped>
.tool-bar {
  background-color: #f6f5f5;
  padding-left: 20px;
}
</style>

<style>
.el-tabs__item.is-active, .el-tabs__item:hover {
  color: rgb(217, 94, 41);
}

.el-tabs__active-bar {
  background-color: rgb(217, 94, 41);
}

.el-tabs__header {
  margin: 0;
}

/*.el-tabs__nav-wrap::after{*/
/*  background-color: transparent;*/
/*}*/
</style>


