<template>
  <div>
    <div style="display: flex; justify-content: space-between">
      <h5 class="setting-header">多孔模型</h5>
      <el-link type="primary" class="show-model-link" @click="showModel" :disabled="model.processing || model.status < 5">显示模型</el-link>
    </div>

    <el-form label-position="right" size="mini" label-width="80px" :disabled="model.processing">
      <el-form-item label="半径">
        <el-input type="number" v-model="form.radii">
        </el-input>
      </el-form-item>
      <el-form-item label="数量">
        <el-input
            type="number"
            onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8"
            v-model="form.num_eles">
        </el-input>
      </el-form-item>
      <el-form-item label="分辨率">
        <el-input
            type="number"
            onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8"
            v-model="form.resolution">
        </el-input>
      </el-form-item>
    </el-form>

    <div style="text-align: right; display: none">
      <el-button type="warning" icon="el-icon-refresh-left" size="mini" round>重置</el-button>
      <el-button type="success" icon="el-icon-upload" size="mini" round>保存</el-button>
      <el-button type="primary" icon="el-icon-video-play" size="mini" round>应用</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ConfigPorousModel",
  props: ["defaultForm", "model"],
  data() {
    return {
      form: JSON.parse(JSON.stringify(this.defaultForm))
      // form: {
        // Porous: {
        //   radii: 0.05,
        //   num_eles: 0,
        //   resolution: 40
        // }
      // }
    }
  },
  methods: {
    showModel() {
      this.$EventBus.$emit("model", {action: "load", model: "porous"});
    }
  }
}
</script>

<style scoped>

</style>


