<template>
  <el-tooltip effect="dark" :content="prompt" placement="bottom">
    <input class="icon" type="image" :src="m_image"
           v-on:mouseover="onMouseOver"
           v-on:mouseout="onMouseOut"
           v-on:click="onclick" alt />
  </el-tooltip>
</template>

<script>
export default {
  name: "image-button",
  props: ['prompt', 'normalImage', 'activeImage', 'onclick'],
  data() {
    return {
      m_image: this.normalImage
    }
  },
  methods: {
    onMouseOver(e){
      this.m_image = this.activeImage;
    },
    onMouseOut(e){
      this.m_image = this.normalImage;
    }
  }
}
</script>

<style scoped>
.icon {
  outline: none;
  margin: 4px 6px;
  padding: 4px;
  height: 20px;
  width: 20px;
  border-radius: 10%;
}

.icon:hover {
  background-color: white;
}
</style>


