<template>
  <div class="menu-bar">
    <el-dialog
        title="上传模型"
        :visible.sync="uploadDialogVisible"
        width="30%"
        center
        :modal-append-to-body=false>
      <el-upload
          drag
          accept="stl"
          name="model"
          :on-error="onUploadError"
          :multiple="false"
          :action="GLOBAL.MODELS_LIST_URL">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传stl文件</div>
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="uploadDialogVisible = false; location.reload()">完成</el-button>
      </span>
    </el-dialog>

    <img class="menu-bar-icon" type="image" src="static/logo.png" alt="" style="background-color: orange"></img>
    <menu-item-button v-for="item in menuItems" :prompt="item.prompt"
                      :active-image="item.activeImage"
                      :normal-image="item.normalImage"
                      :onclick="item.func"
                      :key="item.prompt"/>
  </div>
</template>

<script>
import MenuItemButton from "./buttons/MenuItemButton";

export default {
  name: "MenuBar",
  components: {MenuItemButton},
  data() {
    return {
      uploadDialogVisible:  false,
      menuItems: [
        {
          prompt: "打开",
          normalImage: "static/images/icons/icon_open_normal.png",
          activeImage: "static/images/icons/icon_open_deleted.png",
          func: this.upload_file
        },
        {
          prompt: "保存",
          normalImage: "static/images/icons/icon_save_normal.png",
          activeImage: "static/images/icons/icon_save_deleted.png",
          func: this.empty
        },
        {
          prompt: "导出",
          normalImage: "static/images/icons/icon_export_normal.png",
          activeImage: "static/images/icons/icon_export_deleted.png",
          func: this.empty
        },
        {
          prompt: "撤销",
          normalImage: "static/images/icons/icon_undo_normal.png",
          activeImage: "static/images/icons/icon_undo_deleted.png",
          func: this.empty
        },
        {
          prompt: "重做",
          normalImage: "static/images/icons/icon_redo_normal.png",
          activeImage: "static/images/icons/icon_redo_deleted.png",
          func: this.empty
        },
      ]
    }
  },
  methods: {
    upload_file() {
      this.uploadDialogVisible = true;
    },
    empty() {
      // TODO
      alert("empty")
    },

    onUploadError(e){
      this.$message.error('上传文件出错');
      console.error(e);
    }
  }
}
</script>

<style scoped>
.menu-bar {
  background-color: #323334;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
}

.menu-bar-icon {
  outline: none;
  margin: 4px 6px;
  padding: 4px;
  height: 18px;
  width: 18px;
  border-radius: 10%;
}

.menu-bar-icon:hover {
  background-color: white;
}

</style>


