<template>
  <div>
    <div style="display: flex; justify-content: space-between">
      <h5 class="setting-header">仿真模型</h5>
<!--      <el-link type="primary" class="show-model-link" @click="showModel">显示模型</el-link>-->
    </div>

    <el-form label-position="right" size="mini" label-width="80px">
      <el-form-item label="仿真方法">
        <el-radio class="setting-input" v-model="sim" label="1">GPU+MGCG</el-radio>
        <el-radio class="setting-input" v-model="sim" label="2">均一化仿真</el-radio>
        <el-radio class="setting-input" v-model="sim" label="3">BNBS线性仿真</el-radio>
        <el-radio class="setting-input" v-model="sim" label="4">BNBS非线性仿真</el-radio>
      </el-form-item>
    </el-form>

    <div style="text-align: right; display: none">
      <el-button type="warning" icon="el-icon-refresh-left" size="mini" round>重置</el-button>
      <el-button type="success" icon="el-icon-upload" size="mini" round>保存</el-button>
      <el-button type="primary" icon="el-icon-video-play" size="mini" round>应用</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ConfigSimulation",
  // props: ["defaultForm", "model"],
  data() {
    return {
      sim: "1"
      // form: JSON.parse(JSON.stringify(this.defaultForm))
      // form: {
      // Porous: {
      //   radii: 0.05,
      //   num_eles: 0,
      //   resolution: 40
      // }
      // }
    }
  },
  methods: {
    showModel() {
      this.$EventBus.$emit("model", {action: "load", model: "porous"});
    }
  }
}
</script>

<style scoped>

</style>



