const BASE_URL = "/"
const MODELS_LIST_URL = BASE_URL + "models/"
const MODEL_ITEM_URL = function (id){
  return BASE_URL + "models/" + id;
}
const MODEL_CONFIG_URL = function (id){
  return MODEL_ITEM_URL(id) + "/config";
}
const MODEL_TASK_URL = function (id){
  return MODEL_ITEM_URL(id) + "/task";
}


export default {
  BASE_URL,
  MODELS_LIST_URL,
  MODEL_ITEM_URL,
  MODEL_CONFIG_URL,
  MODEL_TASK_URL
}


