<template>
  <div v-loading="loading"
       element-loading-text="Loading"
       element-loading-spinner="el-icon-loading"
       element-loading-background="rgba(0, 0, 0, 0.8)"
       v-if="active_id !== null && active_id !== undefined">

    <div class="panel-side-name" style="font-size: 16px; display: flex; justify-content: space-between">
      <strong>{{ file.name }}</strong>
      <div>
        <el-link type="primary" class="clear-model-link" @click="loadData">刷新</el-link>
        <el-link type="danger" class="clear-model-link" @click="clearModel">清除模型</el-link>
      </div>
    </div>
    <!--    <form-create :rule="rule" v-model="data" :option="options"/>-->
    <div class="setting-panel" v-if="form !== null">
      <config-triangular-mesh class="setting-part"/>
      <el-divider></el-divider>
      <config-tetrahedral-mesh class="setting-part" ref="TetGeneration"
                               :default-form="form.TetGeneration" :model="file"/>
      <el-divider></el-divider>
      <config-tetrahedral-polycube class="setting-part" ref="TetPolycube"
                                   :default-form="form.TetPolycube" :model="file"/>
      <el-divider></el-divider>
      <config-hexahedral-polycube class="setting-part" ref="HexPolycube"
                                  :default-form="form.HexPolycube" :model="file"/>
      <el-divider></el-divider>
      <config-hexahedral-mesh class="setting-part" ref="HexGeneration"
                              :default-form="form.HexGeneration" :model="file"/>
      <el-divider></el-divider>
      <config-porous-model class="setting-part" ref="Porous"
                           :default-form="form.Porous" :model="file"/>
      <el-divider></el-divider>
      <config-simulation class="setting-part" ref="Porous"/>

      <el-divider></el-divider>

      <config-optimizer class="setting-part"/>
    </div>

    <el-divider></el-divider>

    <div style="text-align: center">
      <el-button type="success" icon="el-icon-upload" size="small" round @click="saveParams" :disabled="file.processing">全部保存</el-button>
      <el-button type="primary" icon="el-icon-video-play" size="small" round @click="saveParams(applyTask)" :disabled="file.processing">全部应用</el-button>
    </div>
    <div style="text-align: center; font-size: 12px; color: #8c939d" v-if="file.processing">
      模型任务执行中，无法再次进行修改
    </div>
    <div style="height: 5px"></div>
  </div>

  <div v-else
       style="width: 100%; height: 80%; display: flex; flex-direction: column; text-align: center; justify-content: center; align-items: center">
    <strong style="font-size: 40px;color: #8c939d"><i class="el-icon-folder-opened"></i></strong>
    <h4 style="font-size: 16px; color: #8c939d">请在右侧选择文件</h4>
  </div>
</template>
<script>

import ConfigTriangularMesh from "./setting-configs/ConfigTriangularMesh";
import ConfigTetrahedralMesh from "./setting-configs/ConfigTetrahedralMesh";
import ConfigTetrahedralPolycube from "./setting-configs/ConfigTetrahedralPolycube";
import ConfigHexahedralPolycube from "./setting-configs/ConfigHexahedralPolycube";
import ConfigHexahedralMesh from "./setting-configs/ConfigHexahedralMesh";
import ConfigPorousModel from "./setting-configs/ConfigPorousModel";
import ConfigSimulation from "./setting-configs/ConfigSimulation";
import ConfigOptimizer from "./setting-configs/ConfigOptimizer";

export default {
  name: "SettingPanel",
  components: {
    ConfigOptimizer,
    ConfigSimulation,
    ConfigPorousModel,
    ConfigHexahedralMesh,
    ConfigHexahedralPolycube, ConfigTetrahedralPolycube, ConfigTetrahedralMesh, ConfigTriangularMesh
  },
  data() {
    return {
      active_id: null,
      loading: false,
      file: {},
      data: {},
      form: null,
      defaultForm: null
      // defaultForm: {
      //   TetGeneration: {
      //     target_vertex: 100
      //   },
      //   TetPolycube: {
      //     alpha: 6,
      //     beta: 4,
      //     eq_tol: 0,
      //     eps: 0.1,
      //     start_iter: 20,
      //     max_iter: 300,
      //     iter_step_scal: 1.4,
      //     global_iter: 4,
      //     TetCubePerfector: {
      //       smooth_boundary: 5,
      //       smooth_surface: 20,
      //       smooth_volume: 20
      //     },
      //   },
      //   HexPolycube: {
      //     scaling_X: 0.055,
      //     scaling_Y: 0.055,
      //     scaling_Z: 0.055,
      //     merge_threshold: 0.2,
      //     HexCubePerfector: {
      //       smooth_boundary: 5,
      //       smooth_surface: 20,
      //       smooth_volume: 20
      //     },
      //     Pillowing: {
      //       X: true,
      //       Y: true,
      //       Z: false
      //     },
      //   },
      //   HexGeneration: {
      //     HexPerfector: {
      //       smooth_boundary: 1,
      //       smooth_surface: 20,
      //       smooth_volume: 20
      //     }
      //   },
      //   Porous: {
      //     radii: 0.09,
      //     num_eles: 0,
      //     resolution: 40
      //   }
      // }
    }
  },
  mounted() {
    this.form = JSON.parse(JSON.stringify(this.defaultForm))
    this.active_id = this.$route.params.id;
    if (this.active_id === null || this.active_id === undefined) {
      return;
    }
    this.loadData();
  },
  methods: {
    loadData() {
      this.defaultForm = null;
      this.form = null;
      this.loading = true;
      this.axios.get(this.GLOBAL.MODEL_ITEM_URL(this.active_id)).then(response => {
        let data = response.data;
        if (data.status !== 200) {
          this.$message.error('错误：' + data.msg);
          this.loading = false;
          return;
        }
        this.file = data.data;
        // config file
        this.axios.get(this.GLOBAL.MODEL_CONFIG_URL(this.active_id)).then(response => {
          let data = response.data;
          if (data.status !== 200) {
            this.$message.error('错误：' + data.msg);
            this.loading = false;
            return;
          }
          this.defaultForm = data.data;
          this.form = JSON.parse(JSON.stringify(this.defaultForm));
          this.loading = false;
        }).catch(error => {
          this.$message.error('文件配置加载异常：' + error);
          this.loading = false;
        })
      }).catch(error => {
        this.$message.error('文件属性加载异常：' + error);
        this.loading = false;
      })
    },
    clearModel() {
      this.$EventBus.$emit("model", {action: "clear"});
    },
    saveParams(cb) {
      this.loading = true;
      let data = {
        TetGeneration: this.$refs.TetGeneration.form,
        TetPolycube: this.$refs.TetPolycube.form,
        HexPolycube: this.$refs.HexPolycube.form,
        HexGeneration: this.$refs.HexGeneration.form,
        Porous: this.$refs.Porous.form,
      }
      this.axios.post(this.GLOBAL.MODEL_CONFIG_URL(this.active_id), data).then(response => {
        let data = response.data;
        if (data.status !== 200) {
          this.$message.error('错误：' + data.msg);
          this.loading = false;
          return;
        }
        this.$message.success('参数提交成功');
        (cb && typeof(cb) === "function") && cb();
        this.loading = false;
      }).catch(error => {
        this.$message.error('参数提交异常: ' + error);
        this.loading = false;
      })
    },
    applyTask(){
      this.loading = false;
      this.axios.post(this.GLOBAL.MODEL_TASK_URL(this.active_id)).then(response => {
        let data = response.data;
        if (data.status !== 200) {
          this.$message.error('错误：' + data.msg);
        }else {
          this.$message.success('任务成功执行，请耐心等待...');
        }
        this.loading = false;
        location.reload();

      }).catch(error => {
        this.$message.error('参数提交异常: ' + error);
        this.loading = false;
      })
    }
  }
}
</script>

<style>
.panel-side-name {
  background-color: rgb(217, 218, 219);
  padding: 5px 10px 5px 15px;
  font-size: 14px;
}

.setting-input {
  margin-top: 2px;
  margin-bottom: 2px;
}

.setting-part {
  /*background-color: #f1f1f1;*/
  border-radius: 2px;
  padding: 1px 5px 1px 5px;
  margin: 5px 0 5px 0;
}

.setting-panel {
  padding: 0 5px 0 5px;
}

.setting-header {
  margin: 5px 0 5px 0;
}

.setting-subheader {
  margin: 12px 0 5px 0;
}

.sublevel {
  margin: 0 0 0 5px;
}

.setting-label {
  font-size: 12px;
  margin: 4px 0;
}

.setting-item {
  font-size: 12px;
  display: flex;
  align-items: center;
  margin: 2px 1px;
}

.indent {
  margin: 0 0 0 10px;
}

.show-model-link, .clear-model-link {
  font-size: 12px;
}

</style>

<style>
.setting-input > .el-input__inner {
  background-color: #F8F9FA;
}

.setting-input > .el-radio__label {
  font-size: 12px;
}

.el-form-item__label {
  font-size: 12px;
}

.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item, .el-form-item {
  margin-bottom: 10px;
}
</style>



